.work-card{
    padding: 8px;
    width: 100%;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
    

}
.work-logo{
    display: flex;

}
.work-info{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}
.work-date{
    color: black;
    font-style: bold;
}
.work-desc{
    text-align:left ;

    /* if information is too long  */
    display: -webkit-box;
    -webkit-line-clamp:2 ;
    -webkit-box-orient: horizontal;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80%;
}
@media only screen and (max-width:720px){
    .work-card{
        width: 100%;

    }

}